import Swiper, { Navigation } from 'swiper';
import { exist } from '../utils';
import 'swiper/css';
import 'ScssComponents/category-titled-slider.scss';
import 'ScssComponents/universal/section-head.scss';
import 'ScssComponents/category-slider.scss';
import 'ScssComponents/category-item.scss';

const categorySlider = (
	sectionSelector = '.js-category-section',
	sliderSelector = '.js-category-slider',
	btnNextSelector = '.js-category-next-button',
	btnPrevSelector = '.js-category-prev-button',
) => {
	const $sections = document.querySelectorAll(sectionSelector);
	if (!exist($sections)) return;

	Swiper.use([Navigation]);

	$sections.forEach($section => {
		const $slider = $section.querySelector(sliderSelector);
		const $btnNext = $section.querySelector(btnNextSelector);
		const $btnPrev = $section.querySelector(btnPrevSelector);
		if (!exist([$slider, $btnNext, $btnPrev])) return;

		const categorySwiper = new Swiper($slider, {
			speed: 800,
			navigation: {
				nextEl: $btnNext,
				prevEl: $btnPrev,
			},
			breakpoints: {
				320: {
					slidesPerView: 1.44,
				},
				768: {
					slidesPerView: 2.6,
				},
				1024: {
					slidesPerView: 3,
				},
			},
		});
	});
};

export default categorySlider;
